#auth-container {
  transform: scale(0.75);
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
#auth-container form {
  margin: 0 auto;
  padding: 5em 2em;
  width: clamp(300px, 40%, 400px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  background: linear-gradient(to top right, rgb(230, 221, 205) 0%, wheat 100%);
  box-shadow: 1px 2px 10px 0px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}
#auth-container form input {
  background-color: transparent;
  width: clamp(200px, 40%, 350px);
  font-size: 1rem;
  aspect-ratio: 10/1;
  padding: 1em 2em;
  border: none;
  border-bottom: 3px solid gray;
}
#auth-container form button {
  width: clamp(300px, 40%, 350px);
  aspect-ratio: 10/1;
  background-color: tomato;
  border: 3px solid tomato;
  border-radius: 10px;
  color: white;
  font-size: 1rem;
  font-weight: 600;
  padding: 1em 2em;
  cursor: pointer;
}
#auth-container form button:hover {
  background-color: transparent;
  color: tomato;
}
#auth-container a {
  text-decoration: underline;
  color: rgba(255, 99, 71, 0.75);
  font-size: 1rem;
  font-weight: 600;
}
#auth-container a:hover {
  color: tomato;
  cursor: pointer;
}

#popup {
  display: none;
  width: max-content;
  height: 100px;
  padding: 1em 2em;
  position: absolute;
  top: 40%;
  right: 40%;
  background-color: rgb(34, 34, 34);
  color: whitesmoke;
}
@media (max-width: 600px) {
  #popup {
    right: 20%;
  }
}
#popup span {
  cursor: pointer;
  width: 20px;
  aspect-ratio: 1/1;
  background-color: whitesmoke;
  color: black;
  border-radius: 30px;
  padding: 0.5em;
  text-align: center;
  position: absolute;
  right: 1em;
  border: 3px solid transparent;
}
#popup span:hover {
  background-color: black;
  color: whitesmoke;
  border-color: whitesmoke;
}
#popup p {
  margin-top: 4em;
}